.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 32px;
    align-items: center;
    justify-content: center;
}

.buttonsContainer {
    display: flex;
    gap: 32px;
}

.like {
    color: var(--greenAccent);
}

.dislike {
    color: var(--lightRedAccent);
}

.rateBtn:hover {
    cursor: pointer;
    -webkit-filter: brightness(85%);
}
